import React, { useState } from "react";
import { Eye, EyeOff, Sparkles, Mail, Lock } from "lucide-react";
import "../../styles/auth.css";
import { useLanguage } from "../../i18n/LanguageContext";

import { UniversalLink as Link } from "../../database/UniversalLink";
import { useUniversalRouter } from "../../database/routerViteGatsbyUniversal";

export function Signup() {
  const { go } = useUniversalRouter();
  const { t } = useLanguage();

  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [showPassword, setShowPassword] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState("");

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setError("");
    setIsLoading(true);

    try {
      await new Promise((resolve) => setTimeout(resolve, 1500));
      go("/app?onboarding=true");
    } catch (err) {
      setError("Failed to create account. Please try again.");
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className="auth-container">
      <div className="auth-bg">
        <div className="auth-gradient-orb orb-1"></div>
        <div className="auth-gradient-orb orb-2"></div>
        <div className="auth-gradient-orb orb-3"></div>
      </div>

      <Link to="/" className="auth-back-link">
        <svg width="20" height="20" viewBox="0 0 20 20" fill="none">
          <path
            d="M15 10H5M5 10L10 15M5 10L10 5"
            stroke="currentColor"
            strokeWidth="2"
            strokeLinecap="round"
            strokeLinejoin="round"
          />
        </svg>
        {t("auth.backHome")}
      </Link>

      <div className="auth-card">
        <div className="auth-header">
          <div className="auth-icon-badge">
            <Sparkles className="auth-sparkle-icon" size={32} />
          </div>
          <h1 className="auth-title">{t("auth.signup.title")}</h1>
          <p
            className="auth-subtitle"
            dangerouslySetInnerHTML={{ __html: t("auth.signup.subtitle") }}
          />
        </div>

        <form onSubmit={handleSubmit} className="auth-form">
          {error && <div className="auth-error">{error}</div>}

          <div className="auth-field">
            <label htmlFor="email" className="auth-label">
              <Mail size={14} />
              {t("auth.emailLabel")}
            </label>
            <div className="auth-input-wrapper">
              <input
                id="email"
                type="email"
                className="auth-input"
                placeholder="you@example.com"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                required
                disabled={isLoading}
              />
            </div>
          </div>

          <div className="auth-field">
            <label htmlFor="password" className="auth-label">
              <Lock size={14} />
              {t("auth.passwordLabel")}
            </label>
            <div className="auth-input-wrapper">
              <input
                id="password"
                type={showPassword ? "text" : "password"}
                className="auth-input"
                placeholder="••••••••"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                required
                minLength={8}
                disabled={isLoading}
              />
              <button
                type="button"
                className="auth-toggle-password"
                onClick={() => setShowPassword(!showPassword)}
                disabled={isLoading}
              >
                {showPassword ? <EyeOff size={18} /> : <Eye size={18} />}
              </button>
            </div>
            <p className="auth-hint">{t("auth.passwordHint")}</p>
          </div>

          <button type="submit" className="auth-submit-btn" disabled={isLoading}>
            {isLoading ? (
              <>
                <span className="auth-spinner"></span>
                {t("auth.signup.submitting")}
              </>
            ) : (
              <>
                <Sparkles size={18} />
                {t("auth.signup.submit")}
              </>
            )}
          </button>

          <p
            className="auth-pricing-note"
            dangerouslySetInnerHTML={{ __html: t("auth.signup.pricingNote") }}
          />
        </form>

        <div className="auth-footer">
          {t("auth.signup.footer").split("?")[0]}?{" "}
          <Link to="/login" className="auth-link">
            {t("auth.signup.footer").split("?")[1]?.trim() || t("common.login")}
          </Link>
        </div>
      </div>
    </div>
  );
}
